
import { splitOnLineBreaks } from '@i-is-as-i-does/jack-js/src/modules/Help'
export default {
  name: 'NxThreadDescription',
  props: {
    description: {
      type: [String, undefined],
      default: ''
    }
  },
  computed: {
    descriptionLines: function () {
      return splitOnLineBreaks(this.description)
    }
  }
}
