
import { getSnippet } from '@i-is-as-i-does/nexus-core/src/data/NxSnippet'
import { vShow, vHide } from '@i-is-as-i-does/valva/src/modules/transitions.js'
import { styleUrl, scriptSrc } from '../../AppDefaults'
export default {
  name: 'NxEmbed',
  props: {
    styleUrl: String,
    src: String,
    closed: Boolean
  },
  watch: {
    closed: function (newval) {
      if (newval && this.open) {
        this.toggleEmbed()
      }
    },
    src: function (newval) {
      if (newval) {
        this.snippets.json = newval
        this.snippets.html = getSnippet(newval, this.style, scriptSrc)
      }
    }
  },
  methods: {
    easeIn: function (e) {
      vShow(e, 'ease')
    },
    easeOut: function (e) {
      vHide(e, 'ease')
    },
    changeSnippet: function (type) {
      if (this.type !== type) {
        this.type = type
      }
    },
    selectContent: function () {
      var range
      if (document.selection) { // IE
        range = document.body.createTextRange()
        range.moveToElementText(this.$refs.snippet)
        range.select()
      } else if (window.getSelection) {
        range = document.createRange()
        range.selectNode(this.$refs.snippet)
        window.getSelection().removeAllRanges()
        window.getSelection().addRange(range)
      }
    },
    toggleEmbed: function () {
      var mk
      if (!this.open) {
        mk = '<\xa0/>'
        this.open = true
      } else {
        mk = this.closeMark
        this.open = false
      }
      this.$refs.mark.textContent = mk
    }
  },
  data () {
    return {
      style: null,
      snippets: { json: this.src, html: getSnippet(this.src, this.style, scriptSrc) },
      open: false,
      closeMark: '</>',
      type: 'html'
    }
  },
  created () {
    if (this.styleUrl !== styleUrl) {
      this.style = this.styleUrl
    }
  }
}
